import "./App.css";
import "bulma/css/bulma.min.css";
import { Button, Form } from "react-bulma-components";
import { useEffect, useState } from "react";
import Complete from "./complete";
import { createClient } from "@supabase/supabase-js";
const supabase = createClient(
  "https://hurbrteqzoptqzpkonng.supabase.co",
  "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Imh1cmJydGVxem9wdHF6cGtvbm5nIiwicm9sZSI6ImFub24iLCJpYXQiOjE2NDQ1OTI2MzIsImV4cCI6MTk2MDE2ODYzMn0.c0mi_DdEG_WZm5AEAZESo3zo9pSfxsq8HqMY3V7omKw"
);
function App() {
  const [dataList, setDataList] = useState(0);
  const [subject, setSubject] = useState("");
  const [times, setTimes] = useState(1);
  const [completeflag, setCompleteflag] = useState(0);
  const [clickable, setClickable] = useState(1);
  const wordList = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  function init() {
    const tmpList = [];
    for (let i = 0; i < 12; i++) {
      tmpList.push(Math.random() >= 0.5);
    }
    setDataList(tmpList);
  }
  useEffect(() => {
    init();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  function makeText() {
    let text = "";
    for (let i = 0; i < dataList.length; i++) {
      if (dataList[i]) {
        text += wordList[i];
        text += "";
      }
    }
    text = text.substr(0, text.length - 1);
    return text;
  }
  function makeNText() {
    let text = "";
    for (let i = 0; i < dataList.length; i++) {
      if (!dataList[i]) {
        text += wordList[i];
        text += "";
      }
    }
    text = text.substr(0, text.length - 1);
    return text;
  }
  async function likeSubmit() {
    setClickable(0);
    const d = { ...dataList };
    d.user = subject;
    d.like = 1;
    const { data, error } = await supabase.from("data").insert([d]);
    console.log(data, error);
    if (timesChange()) {
      init();
      console.log("likeSubmit");
      setClickable(1);
    }
  }
  async function dislikeSubmit() {
    setClickable(0);
    const d = { ...dataList };
    d.user = subject;
    d.like = 0;
    const { data, error } = await supabase.from("data").insert([d]);
    console.log(data, error);
    if (timesChange()) {
      init();
      console.log("dislikeSubmit");
      setClickable(1);
    }
  }
  function timesChange() {
    if (times < 10) {
      setTimes(times + 1);
      return true;
    } else {
      setCompleteflag(1);
      return false;
    }
  }
  if (completeflag) {
    return <Complete />;
  } else {
    return (
      <div className="App">
        <form className="form">
          <Form.Field>
            <Form.Label>
              10
            </Form.Label>
          </Form.Field>
          <Form.Field className="shenfen">
            <Form.Label ></Form.Label>
            <Form.Control>
              <Form.Select
                value={subject}
                onChange={e => {
                  return setSubject(e.target.value);
                }}
              >
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value="vintage">vintage</option>
              </Form.Select>
            </Form.Control>
          </Form.Field>
          <Form.Field className="wenti">
            <Form.Label></Form.Label>
          </Form.Field>
          <Form.Field className="textField">
            <Form.Label size="medium" className="its"></Form.Label>
            <Form.Label size="medium"> {makeText()}</Form.Label>
            <Form.Label size="medium" className="itsnot"></Form.Label>
            <Form.Label size="medium"> {makeNText()}</Form.Label>
          </Form.Field>
          <Form.Field className="submit">
            <Button.Group align={"default"} size={"default"}>
              <Button
                renderAs="span"
                color="success"
                onClick={clickable ? likeSubmit : null}
              >
                
              </Button>
              <Button
                renderAs="span"
                color="danger"
                onClick={clickable ? dislikeSubmit : null}
              >
                
              </Button>
            </Button.Group>
          </Form.Field>
          <p>{times}/10</p>
        </form>
      </div>
    );
  }
}

export default App;
